import { Box, Grid, Typography } from "@mui/material";
import { format } from "date-fns";
import { useEffect, useState } from "react";
import { NavigateFunction, useNavigate } from "react-router-dom";


interface Props {
  notes : Note[]
  category : string
  searchText : string
  order : string
}

const NotesList : React.FC<Props> = ({ notes, category, searchText, order }) : React.ReactElement => {

  const navigate : NavigateFunction = useNavigate();

  const [filteredNotes, setFilteredNotes] = useState<Note[]>(notes);

  //Tulosten filtteröinti
  useEffect(() => {

    const searchResults = notes.filter((note: Note) => {
      return (
        note.contents.toLowerCase().includes(searchText.toLowerCase()) ||
        note.title.toLowerCase().includes(searchText.toLowerCase()) 
      );
    });

    if(category === "#f1eff5"){

      if(order === "Vanhin ensin"){

        setFilteredNotes(searchResults)
        
      }else{

        setFilteredNotes(searchResults.sort((a: Note, b: Note) => b.date.getTime() - a.date.getTime()));
        
      }

    }else{

      if(order === "Vanhin ensin"){

        setFilteredNotes(searchResults.filter((note: Note) => note.color === category));
        
      }else{

       setFilteredNotes(searchResults.filter((note: Note) => note.color === category).sort((a: Note, b: Note) => b.date.getTime() - a.date.getTime()));
        
      }

    }

  }, [category, searchText, order]);

  return (
    <>
     {Boolean(Object.keys(notes).length < 1)
      ? <Typography sx={{ marginTop: "1rem", textAlign: "center" }}>Lisää ensimmäinen muistiinpano.</Typography>
      : <Grid
          container
          spacing={1}
          sx={{
            marginBottom: "1.5rem",
          }}
        >
        {filteredNotes.map( (note : Note, idx: number) =>
          <Grid
            item
            xs={6}
            key={idx}
          >
            <Box
              onClick={() => navigate(`/edit/${note.id}`)}
              sx={{
                  height: "270px",
                  display:"flex",
                  flexDirection: "column",
                  position: "relative",
                  backgroundColor: `${note.color}`,
                  borderRadius: "25px",
                  padding: "1.1rem",
                  boxShadow: 4
                  }}
              >
                {/*Otsikko*/}
              <Typography
                sx={{
                  fontWeight: "bold",
                  fontSize:"1.3rem",
                  marginBottom: "0.9rem",
                  textOverflow: "ellipsis",
                  whiteSpace: "nowrap",
                  overflow: "hidden"
                }}
              >{note.title}</Typography>
              {/*Sisältö*/}
              <Typography
                  sx={{
                    maxWidth: '100%',
                    fontSize:"0.9rem",
                    display: '-webkit-box',
                    WebkitBoxOrient: 'vertical',
                    WebkitLineClamp: 7,
                    overflow: 'hidden',
                    textOverflow: 'ellipsis',
                    whiteSpace: 'pre-line'
                  }}
              >{note.contents}</Typography>
              {/*Päiväys ja aika*/}
              <Typography
                sx={{
                  fontSize:"0.8rem",
                  textAlign:"end",
                  position: "absolute",
                  bottom: "1.1rem",
                  right: "1.1rem"
                }}
              >{format(note.date, "d.M.Y HH.mm")}</Typography>
            </Box>
          </Grid>
        )}
      </Grid>
     }             
  </>
 );
}

export default NotesList;