import { createTheme, ThemeOptions } from "@mui/material";

const themeStyles = {
  palette: {
    mode: 'light',
    primary: {
    main: '#000000',
  },
  secondary: {
    main: '#000000',
    light: '#000000',
  },
  text: {
    primary: '#000000',
    secondary: '#000000',
  },
  },
  typography: {
      body1: {
        fontFamily:[
          'Poppins',
          'sans-serif'
        ].join(','),
        color: '#000000',
        opacity: "80%"
      },
      button: {
        fontFamily:[
          'Poppins',
          'sans-serif'
        ].join(','),
        color: 'white',
        opacity: "90%"
      },
  },
  menuPaper: {
    backgroundColor: "lightblue"
  }
};

const theme = createTheme(themeStyles as ThemeOptions);

export default theme